.title-screen {
  max-width: 800px;
  max-height: 600px;
  display: flex;
}

.title-details {
  position: absolute;
  display: flex;
  flex-direction: column;
  margin-top: 150px;
  margin-left: 80px;
  text-align: center;
  font-size: 20px;
}

.context {
  padding: 20px;
  color: white;
}

#start-button {
  background-color: green;
  color: white;
  padding: 10px 30px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  border-radius: 8px;
  margin-top: 470px;
  margin-left: 450px;
  position: absolute;
}

#start-button:hover {
  box-shadow: 0 10px 10px 0 yellow;
}

